.popupBackground {
    position: fixed;
    background-color: black;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    opacity: 0.8;
  }
  
  .popupContainer {
    background-color: white;
    text-emphasis: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 0px #888;
    padding: 10px;
    border-radius: 10px;
    height: 220px;
    width: 350px;
  }